	module.exports = {"data":{"markdownRemark":{"html":"<p>探検好きな少年は、夜中にランプ片手に村を飛び出した。 昔合作絵企画における、自分担当分のカット。8人で1カットを描き、雑誌に投稿した。葉書の 1/8 区画を埋めるように描いていたが、あるいは8人で1枚の葉書を描くようにしていれば、もうちょっと違う展開になったかも知れない。  残念ながら、その雑誌は今は休刊と言う名の廃刊。</p>","frontmatter":{"cover":null,"date":"1999-06-10T00:00:00+09:00","strDate":"1999/6/9","title":"さまよえる子供"}}},"pathContext":{"path":"/ja/blog/1999/06/10-child/","langKey":"ja"}}

